.Comments h2 {
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-size: 36px;
    text-transform: uppercase;
}

.Comments h2::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  width: 230px;
  height: 3px;
  background: #3f71f4;
  transform: translateX(-50%);
}

.Comments {
  padding: 100px;
}

.Comments button {
    height: auto;
    width: fit-content;
    padding: 0 5px;
    border: 2px solid #28a745;
    background: #28a745;
    color: #fff;
    outline: none;
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
}

.Comments #comment {
  margin: 10px 0;
  height: 100px;
}

.Comments #username {
  height: 45px;
  width: 100%;
  margin-bottom: 10px;
}

@media (max-width: 990px) {
  .Comments {
    padding: 50px;
  }
}